export default {
    data() {
        return {
            mode: {
                sign: {
                    plus: true,
                    minus: true,
                },
                side: {
                    right: true,
                    left: false,
                },
                minSum: 0,
                maxSum: 20,
                minTerm: 0,
                maxOneTerm: 20,
                simplerOrder: null,
            },
            history: {
                lengthMax: 100,
                list: [],
            },
            round: {
                sign: null,
                side: null,
                input: null,
                number: {
                    sum: null,
                    term1: null,
                    term2: null,
                },
                equation: {},
            },
            display: {
                sign: {
                    plus: '+',
                    minus: '-',
                }
            },
            assertResult: null,
            resultFlag: null,
            inputFlag: false,
            resultList: [],
            inputList: [],
        }
    },
    watch: {
        resultFlag() {
            if (this.resultFlag === true) {
                this.hintFlag = true
            }
        }
    },
    methods: {
        historyPack(term1, term2) {
            return [
                Math.max(term1, term2),
                this.display.sign[this.round.sign],
                Math.min(term1, term2),
            ].join('')
        },
        historyCheck(term1, term2) {
            return this.history.list.indexOf(this.historyPack(term1, term2)) === -1
        },
        historyAdd(term1, term2) {
            this.history.list.push(this.historyPack(term1, term2))

            if (this.history.list.length > this.history.lengthMax) {
                this.historyCut(this.history.lengthMax)
            }
        },
        historyCut(size) {
            this.history.list.splice(0, this.history.list.length - size)
        },
        buildSign() {
            this.round.sign = Object.entries(this.mode.sign)
            .filter(e => !!e[1])
            .map(e => e[0])
            .sort(() => 0.5 - Math.random())[0]
        },
        buildSide() {
            this.round.side = Object.entries(this.mode.side)
            .filter(e => !!e[1])
            .map(e => e[0])
            .sort(() => 0.5 - Math.random())[0]
        },
        buildNumbers() {
            const processingMax = 100;
            let term1, term2, processingCounter = 0;
            
            while (processingCounter < processingMax) {

                term1 = Math.floor(Math.random() * ((this.mode.maxOneTerm ?? this.mode.maxSum) - (this.mode.minTerm ?? 0) + 1)) + (this.mode.minTerm ?? 0)
                term2 = Math.floor(Math.random() * (this.mode.maxSum - (this.mode.minTerm ?? 0) + 1)) + (this.mode.minTerm ?? 0)
                if ((term1 + term2) > this.mode.maxSum) continue
                if ((term1 + term2) < (this.mode.minSum ?? 0)) continue

                processingCounter++;

                if (this.historyCheck(term1, term2)) break

                if (processingCounter == processingMax - 1) {
                    this.historyCut(1)
                }
            } 

            this.historyAdd(term1, term2);
            
            // shuffle terms for fix maxOneTerm limitation only for term1
            [term1, term2] = [term1, term2].sort(() => 0.5 - Math.random())

            if (this.mode.simplerOrder === true && term1 < term2) {
                this.round.number.term1 = term2
                this.round.number.term2 = term1
            } else {
                this.round.number.term1 = term1
                this.round.number.term2 = term2
            }
            this.round.number.sum = term1 + term2
        },
    },
}